.nav {
  width: 100%;
  height: 100px;

  display: flex;
  align-items: center;

  font-weight: 500;
  padding-left: 200px;
  font-size: 25px;
}
.nav a {
  color: inherit;
  text-decoration: none;
}
* {
  margin: 0;
}
.pages {
  width: 100%;
  background-color: rgb(157, 160, 157);
  height: 100vh;
  overflow: scroll;
  padding: 30px;
}
.workoutItem-card {
  margin: 15px;
  width: 70%;

  background-color: white;
  padding: 30px;
}
.workoutItem-card h4 {
  color: green;
}
.workoutItem-card p {
  margin: 20px 0px;
}
.home-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.workoute-list {
  width: 50%;
}
form {
  display: flex;
  flex-direction: column;
}
